html, body {
  margin: 0;
  padding: 0;
}

:root {
  --blue: #1877f2;
  --text: #171717;
  --muted: #6b6b6b;
  --link-height: 64px;
  --h-pad: 32px;
  --between: 32px;
}

* {
  box-sizing: border-box;
}

body {
  color: var(--text);
  background: #fff;
  font-family: Roboto, sans-serif;
  font-weight: 500;
}

.header {
  width: 100%;
  padding: 0 var(--h-pad);
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.logo {
  align-items: center;
  text-decoration: none;
  display: inline-flex;
}

.logo img {
  width: auto;
  height: 40px;
  display: block;
}

.nav {
  display: block;
}

.nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__item + .nav__item {
  margin-left: var(--between);
}

.nav__link {
  height: var(--link-height);
  text-transform: uppercase;
  letter-spacing: .02em;
  color: var(--muted);
  text-align: center;
  align-items: center;
  padding: 0;
  text-decoration: none;
  display: flex;
  position: relative;
}

.nav__link:hover, .nav__link.is-active {
  color: var(--blue);
}

.nav__link.is-active:after {
  content: "";
  background-color: var(--blue);
  height: 2px;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
}

.nav__link:focus {
  outline-offset: 2px;
  outline: 2px solid #1877f259;
}
